import { ToolbarGroup,ToolbarButton } from "@wordpress/components"
import {RichText, BlockControls} from "@wordpress/block-editor"
import { registerBlockType } from "@wordpress/blocks"

registerBlockType("fitnessblocktheme/fitnessslideheading",{
    title:"Fitness Slide Heading",
    attributes:{
        text:{type:"string"},
        size:{type:"string",default:"h3"}
    },
    edit:EditComponent,
    save:SaveComponent
})
function EditComponent(props){
    function handleTextChange(x){
        props.setAttributes({text:x});
    }
    return (
        <>
            <BlockControls>

            </BlockControls>
            <RichText allowedFormats={['core/bold','core/italic']} tagName={props.attributes.size} value={props.attributes.text} onChange={handleTextChange}/>
        </>
    );
}
function SaveComponent(props){
    return <RichText.Content tagName={props.attributes.size} value={props.attributes.text} />
}